.Dashboard {
  height:100%;
  display:flex;
  flex-direction:column;
  padding:20px;
  gap:20px;
}

.dashboard-content {
  flex-grow:1;
  overflow-y:scroll;
}

.dashboard-content::-webkit-scrollbar {
  display: none;
}

.dashboard-content {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.error-message {
  background: #fce4e4;
  color: #c0392b;
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
  margin-bottom: 1rem;
  font-size: 0.9rem;
}
