.UploadForm {
  max-width: 600px;
  margin: 0 auto;
  padding: 1rem;
}

/* Step Indicator */
.step-indicator {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.step-indicator .step {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  opacity: 0.4;
}

.step-indicator .step.active {
  opacity: 1;
  font-weight: bold;
}

.step-indicator .step.done {
  opacity: 0.7;
}

.step-indicator .step-num {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  background: #ccc;
  color: #fff;
  font-size: 0.75rem;
}

.step-indicator .step.active .step-num {
  background: #4a90d9;
}

.step-indicator .step.done .step-num {
  background: #5cb85c;
}

.step-indicator .step-label {
  font-size: 0.8rem;
}

/* Upload Steps */
.upload-step {
  text-align:center;
}

.upload-step h3 {
  margin-top: 0;
}

/* Styles the upload button */
::file-selector-button {
  background-color: #2d5016;
  color: white;
  height:100%;
  border: none;
  border-right:1px solid black;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}

.upload-hint {
  color: #666;
  font-size: 0.85rem;
  margin-bottom: 1rem;
}

.upload-error {
  background: #fce4e4;
  color: #c0392b;
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
  margin-bottom: 1rem;
  font-size: 0.85rem;
}

/* Mapping Table */
.mapping-table {
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 1rem;
}

.mapping-row {
  display: flex;
  align-items: center;
  padding: 0.5rem 0.75rem;
  gap: 0.75rem;
}

.mapping-row:not(:last-child) {
  border-bottom: 1px solid #eee;
}

.mapping-row.mapping-header {
  background: #f5f5f5;
  font-weight: bold;
  font-size: 0.8rem;
  text-transform: uppercase;
  color: #666;
}

.mapping-row > span:first-child {
  flex: 1;
}

.mapping-row > select,
.mapping-row > span:last-child {
  flex: 1;
}

.mapping-row select {
  padding: 0.3rem;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.field-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.field-label small {
  color: #999;
  font-size: 0.7rem;
}

.raw-value {
  font-family: monospace;
  font-size: 0.85rem;
}

.value-mapping-group {
  margin-bottom: 1rem;
}

.value-mapping-group h4 {
  margin: 0.5rem 0;
}

/* Preview Table */
.preview-table-wrapper {
  overflow-x: auto;
  margin-bottom: 1rem;
}

.preview-table {
  border-collapse: collapse;
  font-size: 0.8rem;
  width: 100%;
}

.preview-table th,
.preview-table td {
  border: 1px solid #ddd;
  padding: 0.3rem 0.5rem;
  text-align: left;
  white-space: nowrap;
}

.preview-table th {
  background: #f5f5f5;
  font-weight: bold;
}

/* Actions */
.upload-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
  gap: 0.5rem;
}

.upload-actions button {
  padding: 0.4rem 1.2rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  background: #fff;
  cursor: pointer;
}

.upload-actions button:last-child {
  background: #4a90d9;
  color: #fff;
  border-color: #4a90d9;
}

.upload-actions button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Review List */
.review-list {
  list-style: none;
  padding: 0;
}

.review-list li {
  padding: 0.25rem 0;
  font-size: 0.9rem;
}
